.divstyle{
  width:100%;
  height:100%;
}

.Layoutstyle{
  height:100%;
  width:100%;
  min-width: 6.4rem;
  background-image: url(https://5b0988e595225.cdn.sohucs.com/images/20190919/82f9dc5fd0aa47fe8e206e0dd3c340cb.jpeg);
  background-size: auto 140%;
  background-position: center;
  background-repeat: no-repeat;
}

.Headstyle{
  background:rgba(250, 247, 247,0.5);
  width:100%;
  height:1rem;
  text-align: center;
}

Header span{
  font-size: 0.35rem;
  color: rgb(27, 126, 219);
  font-weight: bold;
  position: relative;
  display: block;
  margin: 0 auto;
  top:0px;
  text-align: center;
}
.Contentdiv{
  text-align: center;
  width: 6.6rem;
  padding-top:10px;
  margin: 80px auto 0 auto;
  background:rgba(250, 247, 247, 0.8);
  height: 6.6rem;
}
h2{
  letter-spacing: 0.04rem;
  width: 2.8rem;
  height: 0.24rem;
  margin: 0.16rem auto;
  font-size: 35px;
  font-weight:bold;
  color: rgb(4, 57, 83);
}
input{
  width: 2.15rem;
  height: 0.32rem;
  font-size: 16px;
  margin-top:20px;

}
.divImg{
  width: 270px;
  height: 60px;
  margin: 0 auto;
}
.btn{
  width: 3rem;
  height: 45px;
  font-size: 20px;
  margin-bottom: 10px;
  color:white;
  background: rgb(102, 157, 246);
}
.link{
  font-size:18px ;
}
.divlast{
  font-size: 18px;
  width:300px;
  height:20px;
  margin:0.5rem auto;
  text-align: center;
}
.Footer{
  background:rgba(0,0,0,0.2);
  height:0.6rem;
  line-height: 0.4rem;
  padding:0;
}
.Footer div{
  text-align: center;
  position: relative;
  top:5px;
  font-size: 20px;
  margin:0 auto;
}