.divstyle{
  width:100%;
  height:100%;

}

.Layoutstyle{
  height:100%;
  width:100%;
  background-image: url(https://5b0988e595225.cdn.sohucs.com/images/20190919/82f9dc5fd0aa47fe8e206e0dd3c340cb.jpeg);
  background-size: auto 140%;
  background-position: center;
  background-repeat: no-repeat;
}

.Headstyle{
  background:rgba(250, 247, 247,0.5);
  width:100%;
  height:60px;
}

Header span{
  font-size: 30px;
  color: rgb(27, 126, 219);
  font-weight: bold;
  position: relative;
  left:50px;
  top:0px;
}
.Contentdiv{
  text-align: center;
  width: 6.5rem;
  padding-top:10px;
  margin: 1.1rem auto 0 auto;
  background:rgba(250, 247, 247, 0.8);
  height: 6.4rem;
}
h2{
  letter-spacing: 5px;
  width: 350px;
  height: 30px;
  margin: 20px auto;
  font-size: 35px;
  font-weight:bold;
  color: rgb(4, 57, 83);
}
input{
  width: 270px;
  height: 40px;
  font-size: 16px;
  margin-top:20px;

}
.divImg{
  width: 270px;
  height: 60px;
  margin: 0 auto;
}
.btn{
  width: 250px;
  height: 45px;
  font-size: 20px;
  margin-bottom: 10px;
  color:white;
  background: rgb(102, 157, 246);
}
.link{
  font-size:18px ;
}
.divlast{
  font-size: 18px;
  width:300px;
  height:20px;
  margin:20px auto;
  text-align: center;
}
.Footer{
  background:rgba(0,0,0,0.2);
  height:50px;
  padding:0;
}
.Footer div{
  text-align: center;
  position: relative;
  top:5px;
  font-size: 20px;
  margin:0 auto;
}

.divhead{
  font-size: 35px;
  color:blue;
  margin-top: 40px;
  font-weight: bold;
}
.hland{
  font-size: 30px;
  margin: 10px auto 30px;
  height: 50px;

}

.customcount{
list-style: none;
width:400px;
margin: 0 auto;
font-size: 22px;
padding:0;
}
.customcount li{
  display: inline-block;
  width: 150px;
  line-height: 50px;
  font-weight: bold;
  height: 50px;
  color: rgb(102, 157, 246);
  font-size: 32px;
  margin:8px 20px;
  border-radius: 4px;
  box-shadow: 0 0 0 4px rgba(0,0,0, 0.1);
  background-color:rgba(0,0,0,0.08);
}
.customcount li:hover{
  background-color: rgba(0,102,204,0.6);
  color: aliceblue;
 
}
.btnL{
  width:200px;
  height:50px;
  font-size: 25px;
  margin-top:23px;
}
.customcount li.active{
  background-color: red;
}

